.dorpboxProductsHomepageContainer {
  width: 100%;
  background-color: rgb(251, 252, 252);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.dorpboxProductsHomepageQoute {
  font-size: 36px;
  font-weight: 600;
  font-family: "Sharp Grotesk 23", "Sharp Grotesk 23 KR",
    "Sharp Grotesk 23 Thai", "Helvetica (Sharp Grotesk 23 Fallback)",
    "Arial (Sharp Grotesk 23 Fallback)", -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Helvetica Neue", Helvetica, Arial, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  text-align: center;
}

.productsExplaningContainer {
  width: 73%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 50px; /* Optional: Add gap between the divs */
  padding: 20px;
  margin-bottom: 50px;
  font-family: "Atlas Grotesk Web", "Atlas Grotesk", AtlasGrotesk,
  "Helvetica (Atlas Grotesk Fallback)", "Arial (Atlas Grotesk Fallback)",
  -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Helvetica,
  Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.productsExplaningContainer div h3{
    font-size: 17px;
    font-weight: 600;
}

.productsExplaningContainer div p{

}

.productsExplaningContainer div a {
  color: rgb(6, 6, 6);
  font-size: 18px;
  font-weight: 500;
}

.productsExplaningContainer div a img {
  width: 6%;
}
