.navbar {
  width: 100%;
  height: 47px;
  margin: 0px;
  padding: 0px;
  display: flex;
}
.companyNavbarLogo {
  width: 47px;
  height: 47px;
}

.navbarLinks {
  width: 100%;
  display: flex;
}

.homeLinks {
  width: 11%;
  display: flex;
  text-decoration: none;
}

.homeLinks p {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-left: 10px;
  padding-bottom: 3px;
  font-size: 24px;
  font-weight: 700;
  font-family: inherit;
  color: #000000;
  text-decoration: none;
}

.navbarmiddlecontainer {
  width: 27%;
  margin: 0px;
  margin-left: 16px;
  padding: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.whyDropboxContainer {
  height: auto;
}

.navbarmiddlecontainer button {
  height: 47px;
  margin: 0px;
  padding: 0px;
  font-size: 17px;
  font-weight: 200;
  color: #4a4646;
  border: none;
  /* border: 1px solid #ef0b0b; */
  background-color: transparent;
  cursor: pointer;
}

.navbarmiddlecontainer div {
  height: auto;
  display: flex;
}

.pricingLinkTag {
  font-size: 17px;
  font-weight: 400;
  border: none;
  color: #4a4646;
  background-color: transparent;
  cursor: pointer;
  text-decoration: none;
}

.navbarRight {
  width: 33%;
  margin: 0px;
  padding: 0px;
  margin-left: 29%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbarRight a {
  margin: 0px;
  padding: 0px;
  font-size: 17px;
  font-weight: 400;
  border: none;
  color: #4a4646;
  background-color: transparent;
  cursor: pointer;
  text-decoration: none;
}

.getStartedLink {
  width: 35%;
}
.getStartedLink div {
  height: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #2869cb;

  padding-left: 10%;
  padding-right: 10%;
}

.getStartedLinkText {
  font-size: 17px;
  font-weight: 600;
  font-family: inherit;
  color: white;
}

.getStartedRightarrow {
  width: 20px;
}


